import Header from "./components/Header";
import { Route, Navigate, Routes } from "react-router-dom";
import Ongoing from "./components/Ongoing";
import Upcoming from "./components/Upcoming";
import Leaderboard from "./components/Leaderboard";
import Results from "./components/Results";
import { useAuth0 } from "@auth0/auth0-react";
import LoginPage from "./components/LoginPage";
import { useDispatch, useSelector } from "react-redux";
import Profile from "./components/Profile";
import { useEffect } from "react";
import usersService from "./services/usersService";
import { setUserDetails } from "./store/actions/users";

function App() {
  const dispatch = useDispatch();
  const groups = useSelector((state) => state.users.groups);
  const { user } = useAuth0();

  useEffect(() => {
    if (user){
      usersService.checkIfUserExists(user.email)
      .then(result => {
        if (result.data.length === 0) {
          usersService.createUser(user.given_name || user.nickname, user.email)
          .then(result => {
            const { username, id } = result.data;
            dispatch(setUserDetails(username, id));
          })
        } else {
          const { username, id } = result.data;
          dispatch(setUserDetails(username, id));
        }
      })
      .catch((error) => {
        console.log(error);
      })
    }
  }, [user])

  return (
    <div className="App">
      {!user ? (
        <LoginPage />
      ) : (
        <>
          <Header />
          <Routes>
            <Route path="ongoing" element={<Ongoing />} />
            <Route
              path="upcoming"
              element={<Upcoming />}
            />
            <Route path="results" element={<Results />} />
            <Route
              path="leaderboard"
              element={<Leaderboard />}
            />
            <Route
              path="profile"
              element={<Profile />}
            />
            <Route path="*" element={<Navigate replace to="/upcoming" />} />
          </Routes>
        </>
      )}
    </div>
  );
}

export default App;
